<template>
    <div >
        <shead :headid="$route.query.headid"></shead>

        <div  class="wrap round-block">
            <div class="line-title">
                当前位置：<router-link to="/e/index">首页</router-link> >>  商品信息>>{{shangpin.name }}
            </div>

            <div class="fn-clear"></div>
            <div class="shangpin-info clearfix">
                <div class="pic-area">
                    <div class="big-img">
                        <img :src="hostHead+bigPic"/>
                    </div>
                    <div class="small-imgs owl-carousel">
                        <div @click="select(item,index)"  v-for="(item,index) in shangpin.images " :class="index==selectedIndex?'active item':'item'">
                            <img  :src="hostHead+item"/>
                        </div>
                    </div>
                </div>


                <div class="props">
                    <div class="title">

                        {{shangpin.name}}
                    </div>
                    <div class="subtitle">
                        {{shangpin.subtitle}}
                    </div>
                    <div class="price-block clearfix">


                        <div class="mall-price clearfix">
                            <div class="label">商城价:</div>
                            <div class="price"> ¥ {{shangpin.hyjia}}</div>

                        </div>
                    </div>
                    <div style="margin-top:20px " class="prop">
                        <dl class="clearfix">
                            <dt>库存</dt>
                            <dd>{{shangpin.kucun}}{{shangpin.danwei}}</dd>
                        </dl>
                    </div>

                    <div style="margin-top:20px " class="prop">
                        <dl class="clearfix">
                            <dt> 口味</dt>
                            <dd>{{shangpin.chandi}}</dd>
                        </dl>
                    </div>




                    <div class="prop">
                        <dl class="clearfix">
                            <dt>数量</dt>
                            <dd><el-input-number v-model="count"   :min="1" :max="10" label="描述文字"></el-input-number></dd>
                        </dl>


                    </div>


                    <div class="prop">



                        <el-button-group>


                            <el-button @click="shoucangHandler" type="primary"><i class="fa fa-star"></i>收藏</el-button>


                            <el-button  v-if="shangpin.kucun>0"  @click="toCartHandler" icon="el-icon-plus"  type="danger">
                                加入购物车
                            </el-button>

                        </el-button-group>


                    </div>



                </div>
            </div>


            <div v-if="listRecomment!=null&&listRecomment.length>0" class="video-list">
                <div class="hd">
                    猜你喜欢商品
                </div>
                <div class="bd">

                    <div v-for="item in listRecomment" class="item">
                        <div class="card">
                            <a :href="hostHead+'/e/shangpininfo?id='+item.id">
                                <div class="pic">
                                    <img class="image" :src="hostHead+item.images[0]"/>

                                </div>
                                <div class="name">
                                    {{item.name}}--({{item.tagname}})
                                </div>
                                <div class="discount"></div>
                                <div class="price">¥{{item.hyjia}}</div>
                            </a>
                        </div>
                    </div>

                </div>
            </div>


            <el-tabs  value="first" >
                <el-tab-pane label="商品介绍" name="first">
                    <div class="brief-content" v-html="shangpin.jieshao"></div>
                </el-tab-pane>
                <el-tab-pane label="商品评价" name="second">
                    <div class="spcomment-list">

                        <div  v-for="comment in listComment" class="item clearfix">
                            <div class="lfar">
                                <img :src="hostHead+comment.touxiang" class="photo" />
                            </div>
                            <div class="rgar">
                                <div class="trow">
                                    {{comment.accountname}}({{comment.name}})
                                </div>
                                <div class="trow">
                                    <el-rate :value="comment.cresult"></el-rate>
                                    {{comment.des}}
                                </div>
                            </div>



                        </div>
                        <div v-if="listComment.length==0" class="no-record-tip">
                            <div class="content">
                                <i class="fa fa-warning"></i>暂无相关评论
                            </div>
                        </div>
                    </div>
                </el-tab-pane>
            </el-tabs>


            <el-dialog title="给他留言" :visible.sync="lwDlg">

                <div class="stdrow">
                    <textarea  name="dcontent"  maxlength="500" class="lwta" v-model="dcontent"   placeholder="在线留言" ></textarea>
                </div>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="lwDlg = false">取 消</el-button>
                    <el-button @click="lwHandler" type="primary" >确 定</el-button>
                </div>
            </el-dialog>


            <loginmodal :show.sync="openLogin"></loginmodal>


        </div>
        <bottom></bottom>


    </div>
</template>

<script>
    import Shead from "./shead";
    import Bottom from "./bottom";
    import Loginmodal from "./loginmodal";
    import  ss from "../util/SmartStorage";
    import {VueUtil} from "../util/vueutil";

    export default {
        name: "shangpininfo",
        data(){
            return {
                listComment: [],
                bigPic: "",
                count: 1,
                hostHead:this.$app.contextPath,
                listRecomment: [],
                listCoupon:[],
                selectedIndex: 0,
                shanghu: {},
                shangpin: {},
                openLogin: false,
                lwDlg: false,
                dcontent: "",
            }

        },
        created(){

            let id=this.$route.query.id;
            this.getShangpin(id);
            this.getComment(id);

            //this.getRecomment();

        },
        methods:{
           async ljHandler(couid){
               if(couid==null)
                   return this.$message.error("不能为空");
               if(!this.isLogin) {
                   this.openLogin = true;
                   return;
               }
               let {data:res}=await this.$http.post("admin/courecord/save",{
                   hyid:this.huiyuan.id,
                   "couponid":couid
               });
               if(res!=null&&res.stateCode<0){
                   this.$message.error(res.des);
                   return;
               }
               this.$message.success("领劵成功");

           },
           async  getComment(id){
                let url="admin/spcomment/list";
                let {data:res}=await this.$http.post(url,{
                    spid:id,
                    "ispaged":"-1"
                });
               if(res!=null&&res.stateCode<0){
                   this.$message.error(res.des);
                   return;
               }
               this.listComment=res.data;
            },

            async  getCoupons(){
                let url="admin/coupon/list";
                let {data:res}=await this.$http.post(url,{
                    "ispaged":"-1"
                });
                if(res!=null&&res.stateCode<0){
                    this.$message.error(res.des);
                    return;
                }
                this.listCoupon=res.data;
            },

            getRecomment(){
                let url="admin/shangpin/recomment";
                this.$http.post(url).then(res=>{
                    console.log(res.data);
                    if(res.data!=null&&res.data.data!=null)
                        this.listRecomment=res.data.data;
                });

            },
            select(item,index){
                this.bigPic=item;
                this.selectedIndex=index;
            },
            async getShangpin(id){
                let url="admin/shangpin/info";
                let p1={id:id};
                if(this.hyid!="")
                    p1.hyid=this.hyid;
                let {data:res}=await this.$http.post(url,p1);
                this.shangpin=res.data;
                this.bigPic=this.shangpin.images[0];
            },

            toCartHandler(){
                if(!this.isLogin) {
                    this.openLogin = true;
                    return;
                }

                let cart=new SmartStorage();
                let cartitem={
                    id:this.shangpin.id,
                    name:this.shangpin.name,
                    tupian:this.shangpin.images[0],
                    count:this.count,
                    selected:true,
                    price:this.shangpin.jiage,
                    typename:this.shangpin.typename
                };
                cart.add(this.shangpin.id,cartitem);
                this.$router.push("/e/shopcart");

            },
            async shoucangHandler(){
                if(!this.isLogin) {
                    this.openLogin = true;
                    return;
                }
                let url="admin/shoucang/save";
                console.log("shanppin.id=",this.shangpin.id);

                let {data:res}=await this.$http.post(url,{
                    targetid:this.shangpin.id,
                    targetname:this.shangpin.name,
                    tupian:this.shangpin.images[0],
                    hyid:this.huiyuan.id,
                    xtype:"shangpin",
                    href:"/e/shangpininfo?id="+this.shangpin.id
                });
                if(res!=null&&res.stateCode<0){
                    this.$message.error(res.des);
                    return;
                }
                this.$message.success("收藏成功");
            },

            async getShanghu(id) {

                let util = new VueUtil(this);
                if (id!= "") {
                    let url = "admin/shanghu/load";
                    let {data: res} = await util.http.post(url, {id: id});
                    console.log("res", res);
                    if (res != null && res.data != null)
                        this.shanghu = res.data;
                }
            },//end load

            openLwHandler(){
                if (!this.isLogin){
                    this.openLogin=true;
                    return ;
                }
                this.lwDlg=true;
            },
            async lwHandler(){
                if(this.dcontent==""){
                    alert("输入留言内容");
                    return;
                }
                let url="admin/leaveword/save";
                let res=await  this.$http.post(url,{
                    dcontent:this.dcontent,
                    hyid:this.huiyuan.id,
                    targetid:this.shanghu.id
                });
                if(res.data.stateCode>0){
                    this.dcontent="";
                    this.lwDlg=false;
                    this.$message.success("留言成功");
                }
            },

        },
        computed:{
            huiyuan(){
                return this.$app.mycas.getAccount(this.$app.mycas.huiyuan_key);
            },

            isLogin() {
                return this.$app.mycas.isLogin(this.$app.mycas.huiyuan_key);
            },

        },
        components: {Bottom,Shead,Loginmodal},

    }
</script>

<style scoped>

</style>
