<template>
    <div>

        <div id="searchss">
            <div class="search">
                <div class="logo">
                    <div class="sitetitle">“遇见”预约订餐系统</div>
                </div>
                <div class="sear_input">

                    <input type="text" @keyup.enter="searchHandler" v-model="keyword"  placeholder="请输入关键字">
                    <button @click="searchHandler"><i class="el-icon-search"></i></button>

                </div>

                <div class="sear_fir">

                        <div class="sinup">

                            <route-template v-if="!(isLogin()||isAdminLogin())" url="/admin/login">
                                <span  class="item">登录</span>
                            </route-template>
                            <a>/</a>

                            <route-template url="/e/register">
                                <span  class="item">注册</span>
                            </route-template>

                            <a>/</a>
                            <route-template url="/e/shopcart">
                                <span  class="item">购物车</span>
                            </route-template>

                        </div>


                    <template v-if="isLogin()">
                        <el-dropdown style="padding:2px 10px; ">
                      <span class="el-dropdown-link">
                          {{huiyuan.accountname}}--{{huiyuan.name}}
                           <i class="el-icon-arrow-down el-icon--right"></i>
                      </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item>
                                    <route-template url="/huiyuan/index">
                                        <i class="fa fa-user"></i>账户信息
                                    </route-template>
                                </el-dropdown-item>

                                <el-dropdown-item divided></el-dropdown-item>
                                <el-dropdown-item @click.native="exitHandler(2)"><span class="exit">退出</span></el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </template>

                    <template v-if="isAdminLogin()">
                        <el-dropdown style="padding:2px 10px; ">
                      <span class="el-dropdown-link pointer">
                          {{users.username}}--{{users.name}}(管理员)
                        <i class="el-icon-arrow-down el-icon--right"></i>
                      </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item>
                                    <route-template url="/admin/index">
                                        <i class="fa fa-user"></i>账户信息
                                    </route-template>

                                </el-dropdown-item>

                                <el-dropdown-item divided></el-dropdown-item>
                                <el-dropdown-item @click.native="exitHandler(1)"><span class="exit">退出</span></el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </template>

                </div>
            </div>
        </div>

        <div style="height:100px;"></div>
        <div id="navall">
            <div id="nav">

                <!--导航-->
                <ul id="inav">

                    <li v-for="sn in listSitenav" :key="sn.id"  >

                        <span v-if="withParam(sn.href)" :class="{active:sn.id==headid,item:true}"  @click="navigatePage(sn.href+'&headid='+sn.id)" >{{sn.title}}</span>
                        <span v-else :id="sn.id" :class="{active:sn.id==headid,item:true}" @click="navigatePage(sn.href+'?headid='+sn.id)" >{{sn.title}}</span>
                    </li>


                </ul>

            </div>
        </div>


        <loginmodal :show.sync="logindlg"></loginmodal>


    </div>
</template>

<script>
    import Loginmodal from "./loginmodal";
    export default {
        name: "shead",
        components: {Loginmodal},
        data() {
            return {
                listSitenav: [],
                keyword:"",
                logindlg: false,
                hostHead: this.$app.contextPath
            }
        },
        methods: {
            withParam(href){
                if(href==null)
                    return  false;
                return  href.split("?").length>1
            },
            async list() {
                let url = "admin/sitenav/list";
                let param = {
                    "ispaged": "-1",
                    "order": " order by id desc"
                };
                let {data: res} = await this.$http.post(url, param);
                if (res.data != null) {
                    console.log("header res=", res);
                    this.listSitenav = res.data;
                }
            },
            navigatePage(url){
                if(url==null||url=="")
                    return ;
                this.$router.push(url);
                this.$app.key=url;

            },
            isLogin() {
                return this.$app.mycas.isLogin(this.$app.mycas.huiyuan_key);
            },
            isAdminLogin() {
                return this.$app.mycas.isLogin(this.$app.mycas.users_key);
            },
            async exitHandler(type) {
                this.$confirm('确定要离开?', '系统提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(async (r) => {
                    let {data: res} = await this.$http.post("admin/admin/exit", {
                        usertype: type
                    });
                    if (res != null && res.stateCode < 0) {
                        this.$message.error(res.des);
                        return;
                    }
                    if(type==2)
                        this.$app.mycas.logout(this.$app.mycas.huiyuan_key);
                    if(type==1)
                        this.$app.mycas.logout(this.$app.mycas.users_key);
                    window.location.reload();

                }).catch(e => {
                });
            },
            searchHandler() {
                if (this.keyword==""){
                    this.$message.error("请输入搜索内容");
                    return;
                }
                this.$router.push(`/e/search?title=${this.keyword}`);

            }

        },
        created() {
            this.list();
        },
        props: {
            headid: {
                type: String,
                default: "1"
            }
        },
        computed:{
           huiyuan(){
              return  this.$app.mycas.getAccount(this.$app.mycas.huiyuan_key);
           },
           users(){
               return this.$app.mycas.getAccount(this.$app.mycas.users_key);
           }
        }
    }
</script>

<style scoped>

</style>
